import { Link, useNavigate } from 'react-router-dom'
import "./ChangePassword.css"
import Union from "../Union/Union"
import { useState } from 'react'
import { useForm } from 'react-hook-form'

const ChangePassword = () => {
    const navigate = useNavigate()
    const [isShowPassword, setIsShowPassword] = useState(false)
    const [isShowPasswordReType, setIsShowPasswordRetype] = useState(false)
    const { register, handleSubmit, formState: { errors } } = useForm()

    // show/hide password "mt khu mi"
    const handleShowHidePassword = () => {

        setIsShowPassword(!isShowPassword);

    };
    // show/hide password "nhp li mt khu mi"
    const handleShowHidePasswordReType = () => {

        setIsShowPasswordRetype(!isShowPasswordReType);

    };

    const onHandleSubmit = (e) => {
        navigate("/change-password-success")
    }

    return (
        <div>
            <div className="Reset-container">
            <Link to="/setting">
                <button className="previous-button">
                    <i className="fal fa-chevron-left icon-back"></i>
                </button>
            </Link>
                
                <div className="icon-login-success">

                </div>
                <div className="Login-status">
                    <div className="login-info">ng nhp ln u thnh cng</div>
                    <div className="login-retpye-password">Vui lng nhp li mt khu mi</div>
                </div>

                <div className="form-input">
                    <form className="w3-container input-form" onSubmit={handleSubmit(onHandleSubmit)}>
                        <p>
                            <label className="input-title">Mt khu mi</label>
                            <input
                                className="w3-input input-user-password"
                                type={isShowPassword ? "text" : "password"}
                                name='newPassword'
                                {...register("newPassword", { required: true, minLength: 6 })}
                            />
                            {Object.keys(errors).length !== 0 && (
                                <>
                                    <span>
                                        {errors.newPassword?.type === "required" && <span className='text-warning-msg'>Vui lng nhp "mt khu"</span>}
                                    </span>
                                    <span>
                                        {errors.newPassword?.type === "minLength" && <span className='text-warning-msg'> "mt khu" ti thiu 6 k t</span>}
                                    </span>
                                </>
                            )}

                            <i className="fas fa-eye-slash  input-icon" onClick={handleShowHidePassword}></i>
                        </p>

                        <p className='div-input-password'>
                            <label className="input-title">Nhp li mt khu mi</label>
                            <input
                                className="w3-input input-user-password"
                                type={isShowPasswordReType ? "text" : "password"}
                                name='retypePassword'
                                {...register("retypePassword", { required: true, minLength: 6 })}
                            />
                            {Object.keys(errors).length !== 0 && (
                                <>
                                    <span>
                                        {errors.retypePassword?.type === "required" && <span className='text-warning-msg'>Vui lng nhp "mt khu"</span>}
                                    </span>
                                    <span>
                                        {errors.retypePassword?.type === "minLength" && <span className='text-warning-msg'> "mt khu" ti thiu 6 k t</span>}
                                    </span>
                                </>

                            )}
                            <i className="fas fa-eye-slash input-icon-2" onClick={handleShowHidePasswordReType}></i>
                        </p>

                        <p className='div-btn-login'>
                            <button type='submit' className="btn-login">i mt khu</button>
                        </p>

                    </form>



                </div>
                <Union />
            </div>

        </div>
    )
}

export default ChangePassword 