import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import './Recharge.css'

export default function Recharge() {

    const { register, handleSubmit, formState: { errors } } = useForm()
    const [coins, setCoins] = useState('')
    const navigate = useNavigate()

    // hm submit form np im
    const onHandleSubmit = (e) => {
        console.log(e);
        navigate("/recharge-confirm")
    }
    // hm thm du phy vo hng nghn khi user nhp s im mun np
    const handleChangeCoin = (event) => {
        const {value} = event.target
        setCoins(value.replaceAll(",", ''))
    }

    return (
        <div className='recharge-container'>
            <Link to="/account-user">
                <button className="previous-button">
                    <i className="fal fa-chevron-left icon-back"></i>
                </button>
            </Link>
            <div className="Recharge-title">
                Np im vo ti khon
            </div>

            <div className="Recharge-guide-1">
                Bc 1: <br />
                Np tin vo ti khon ti bt k chi nhnh no ca ngn hng BIDV. Ghi m s nhn vin trong phn ni dung np im.
            </div>

            <div className="Recharge-guide-2">
                Bc 2: <br />
                in thng tin v nhn nt "Np im" bn di.
            </div>

            <div className="recharge-information">
                <form className="w3-container input-form" onSubmit={handleSubmit(onHandleSubmit)}>
                    <p>
                        <label className="input-title">M giao dch np tin</label>
                        <input
                            className="w3-input input-user-password"
                            type="text"
                            name='tradingCode'
                            {...register("tradingCode", { required: 'Vui lng nhp "M giao dch np tin"' })}
                        />
                        
                        {errors.tradingCode && <span className='text-warning-msg '>{errors.tradingCode.message}</span>}

                    </p>

                    <p className='div-input-password'>
                        <label className="input-title">S im mun np</label>
                        <input
                            className="w3-input recharge-input-coin"
                            type="text"
                            value={coins.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                            name='moneyCoin'
                            {...register("moneyCoin", { required: 'Vui lng nhp s im m bn mun np', onChange: handleChangeCoin })}
                        />
                        <span className='recharge-text-coin'>im</span>
                        
                        {errors.moneyCoin && <span className='text-warning-msg '>{errors.moneyCoin.message}</span>}
                    </p>

                    <p className='div-btn-login'>
                        <button type='submit' className="btn-login">Np im</button>
                    </p>

                </form>
            </div>


        </div>
    )
}   